const prepositions = {
    "": {
        "type": "preposition"
    },
    "": {
        "type": "preposition"
    },
    "": {
        "type": "preposition"
    },
    "": {
        "type": "preposition"
    },
    "": {
        "type": "preposition"
    }
} as const;

export function isPreposition(word: string): word is keyof typeof prepositions {
    return word in prepositions;
}



export default prepositions;